import React from 'react';
import { BrowserRouter as Router,Switch, Route } from 'react-router-dom';

import { Home } from '../Containers/Home'
import ColorfulLife from '../Containers/ColorfulLife'
import { Favourites } from '../Containers/Favourites'
import { Introduction } from '../Containers/Introduction'


export const Routes = () => {
    return ( 
        <Switch>
            <Route exact path='/' component={Home}/>
            <Route path='/home' component={Home}/>
            <Route path='/about/colorfullift' component={ColorfulLife}/>
            <Route path='/about/favourites' component={Favourites}/>
            <Route path='/about/introduction' component={Introduction}/>
        </Switch>
    )
}




